<template>
  <canvas ref="container" style="width: 100%;height: 100%" :style="{}"></canvas>
</template>

<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";
export default {
  name: "threeTexture",
  props: {
    h: Number
  },
  data() {
    return {
      render: null,
      camera: null,
      scene: null,
      player: true,
      controll: null,
      gltfFile: null,
      cube: null,
      cameraOpt: {
        angle: 70,
        min: 0.1,
        max: 1000
      }
    };
  },
  mounted() {
    this.renderAll();
  },
  methods: {
    renderAll() {
      this.renderInit();
      this.renderGLTF();
      this.animation();
    },
    renderInit() {
      const w = this.$refs.container.clientWidth;
      const h = this.$refs.container.clientHeight;
      this.render = new THREE.WebGLRenderer({
        canvas: this.$refs.container
      });
      this.render.setSize(w, h);
      this.camera = new THREE.PerspectiveCamera(
        this.cameraOpt.angle,
        w / h,
        this.cameraOpt.min,
        this.cameraOpt.max
      );
      this.camera.position.set(0, 0, 40);
      this.camera.lookAt(0, 0, 0);
      this.scene = new THREE.Scene();
      this.scene.background = new THREE.Color(0xff0000);
      this.controll = new OrbitControls(this.camera, this.render.domElement);

      const ambientLight = new THREE.AmbientLight(0xffffff);
      ambientLight.castShadow = true;
      this.scene.add(ambientLight);
      //
      const light = new THREE.SpotLight(0xffffff);
      light.position.set(0, 0, 180);
      light.castShadow = true;
      this.scene.add(light);
      const light2 = new THREE.SpotLight(0xffffff);
      light2.position.set(0, 180, 0);
      light2.castShadow = true;
      this.scene.add(light2);
    },
    loadImgTexture(loader, imgurl, obj) {
      loader.load(imgurl, function(image) {
        obj = typeof obj == "string" ? eval(obj) : obj;
        obj.image = image;
        obj.needsUpdate = true;
      });
    },
    loadObjTexture(loader, imgurl, texture, x, onProgress, onError) {
      const that = this;
      loader.load(
        imgurl,
        object => {
          console.log(texture);
          object.traverse(function(child) {
            if (child instanceof THREE.Mesh) {
              child.material.map = texture;
            }
          });
          object.scale.x = 1;
          object.scale.y = 1;
          object.scale.z = 1;
          object.position.set(0, 0, 0);
          object.castShadow = true;
          object.receiveShadow = true;
          that.scene.add(object);
        },
        onProgress,
        onError
      );
    },
    renderGLTF() {
      const manager = new THREE.LoadingManager();
      manager.onProgress = function(item, loaded, total) {
        console.log(item, loaded, total);
      };
      // const onProgress = function(xhr) {
      //   if (xhr.lengthComputable) {
      //     const percentComplete = (xhr.loaded / xhr.total) * 100;
      //     console.log("" + percentComplete + "%");
      //   }
      // };
      //
      // const onError = function(xhr) {
      //   console.log(xhr);
      // };
      const imgloader = new THREE.ImageLoader(manager);
      // const loader = new OBJLoader(manager);
      // const dianluban1 = new THREE.Texture();
      // this.loadImgTexture(imgloader, "/gltf/fsjg08.jpg", dianluban1);
      // this.loadObjTexture(
      //   loader,
      //   "/gltf/shebei01.obj",
      //   dianluban1,
      //   100,
      //   onProgress,
      //   onError
      // );
      const loader = new GLTFLoader(manager);
      // Optional: Provide a DRACOLoader instance to decode compressed mesh data
      var dracoLoader = new DRACOLoader();
      dracoLoader.setDecoderPath("/gltf/draco/");
      loader.setDRACOLoader(dracoLoader);
      console.log(1);
      loader.load(
        "/gltf/glb/chassis.glb",
        gltf => {
          gltf.scene.scale.set(20, 20, 20);
          const box = new THREE.Box3().setFromObject(gltf.scene);
          const center = box.getCenter(new THREE.Vector3());
          gltf.scene.position.x += gltf.scene.position.x - center.x;
          gltf.scene.position.y += gltf.scene.position.y - center.y;
          gltf.scene.position.z += gltf.scene.position.z - center.z;
          const dianluban1 = new THREE.Texture();
          this.loadImgTexture(imgloader, "/gltf/glb/metrial.png", dianluban1);
          gltf.scene.children.forEach(item => {
            item.material = new THREE.MeshStandardMaterial({
              map: dianluban1
            });
          });
          this.scene.add(gltf.scene);
        },
        undefined,
        function(e) {
          console.error(e);
        }
      );
    },
    animation() {
      this.controll.update();
      if (this.cube) {
        this.cube.rotation.x += 0.01;
        this.cube.rotation.y += 0.01;
      }
      this.render.render(this.scene, this.camera);
      requestAnimationFrame(() => {
        if (this.player) {
          this.animation();
        }
      });
    }
  }
};
</script>

<style scoped></style>
